@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}
:root {
  --primary: #5e81f4;
  --secondary: #b8cae8;
  --white: #ffffff;
}
body {
  margin: 0;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5fb;
}

body,
button,
input {
  font-family: "Inter", sans-serif;
  font-size: 1em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: grid;
  grid-template-columns: 0.5fr 2fr 0.5fr;
}
#lateral-2{
  padding: 40px;
  width: 380px;
  background-color: rgba(255, 255, 255, .6);
  box-shadow: 1px 1px 5px 2px rgba(0, 0, 0, .4);
}
#lateral-2 .profile{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.profile_square {
  width: 50px;
  height: 50px;
  margin-top:20px;
  margin-bottom: 20px;
  margin-right: 10px;
}

.profile_square img {
  width: 100%;
  height: 100%;
}
#lateral-2 .progress h3{
  font-size: 18px;
}
#lateral-2 .progress .item-progress article{
  display: flex;
  justify-content: space-around;
  flex-direction: row;

  margin:10px auto;
}

#lateral-2 .progress .item-progress article .icon, .itemtask .icon{
  width: 48px;
  height: 48px;
  border-radius:8px;

  display: flex;
  justify-content: center;
  align-items:center;
  margin-right: 15px;
}
.itemtask{
  display: flex;
  flex-direction: row;
  margin:10px auto;
}
.itemtask .data h4{
  font-size: 14px;
}
.itemtask .data span{
  font-size: 12px;
  color:#83919E;
}
/* DEFAULT */
.button {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 15px 25px;
  border: 0;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  color: #222;
  max-width: 220px;
  transition: 500ms;
}
.button_small {
  color: #222;
  font-size: 16px;
  max-width: 46px;
  max-height: 46px;
  display: flex;
  border: 0;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  transition: 500ms;
  padding: 10px;
}
.button_small:hover,
.button:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}
.button_play {
  background-color: rgba(255, 255, 255, 0.3);
  cursor:pointer;
  color:#fff;
}
.button_primary {
  color: #fff;
  background-color: #5e81f4;
}
.formGroup {
  display: flex;
  flex-direction: column;
}
.formControl {
  max-width: 620px;
  border-radius: 10px;
  border: 0px;
  padding: 15px;
  background-color: #ffffff;
  background-color: var(--white);
}
.formControl::-webkit-input-placeholder {
  color: #ccd6e8;
}
.formControl:-ms-input-placeholder {
  color: #ccd6e8;
}
.formControl::-ms-input-placeholder {
  color: #ccd6e8;
}
.formControl::placeholder {
  color: #ccd6e8;
}



#central{
    max-width: 900px;
    margin:0 15px;
  }
  #central header{
    display: flex;
    justify-content: space-between;
    padding: 30px;  
  }
  
  #central header div ul{
    display: flex;
    flex-direction: row;
    list-style: none;
  }
  #central header div ul li {
    margin:0 10px;
    font-size:17px;
    color:rgba(118,132,146,.8);
    font-weight: 600;
  }
  #central header div ul li.active{
    color:#5E81F4;
  }
  
  #central .last_course{
    width:100%;
    padding: 30px;
    background:url('https://i.ibb.co/SmC76Sn/frame01.png');
    height: 363px;
    border-radius: 10px;
    background-size: cover;
    background-color:#5E81F4;
    position: relative;
  }
  #central .last_course header{
    top:0;
    left: 0;
    position: absolute;
    width: 100%;
  }
  
  #central .last_course header div{
    display: flex;
    flex-direction: column;
    color:var(--white);
  }
  #central .last_course footer{
    bottom:0;
    left: 0;
    position: absolute;
    width: 100%;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .profile_circle img{
    border-radius: 100%;
    border:2px solid var(--white);
  }
  #central .container_course{
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  #central .container_course .course{
    margin: 20px auto ;
    padding: 30px 40px;
    width: 100%;
    height: 311px;
    max-width: 415px;
    border-radius:10px;
    background-size: cover;
  
    position:relative;
  }
  
  #central .container_course .course header{
    display: flex;
    width:100%;
    color:var(--white);
    justify-content: space-between;
    align-items: center;
  
    position: absolute;
    top:0;
    left:0;
    padding:30px;
  }
  #central .container_course .course footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  
    position: absolute;
    bottom:0;
    left: 0;
    padding:10px 30px;
  }
  
  #central .container_course .course header div{
    display: flex;
    flex-direction: column;
  }

.lateral-1{
    max-width: 280px;
    background-color:#FFFFFF;
    height: 100vh;
  
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  .lateral-1 div{  display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .lateral-1 footer strong{ text-align: center; margin:5px 0;  }
  .lateral-1 footer{  
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
  
  
    margin-bottom:80px;
    max-width: 220px;
    background-color:#BFC9ED;
    color:#778DAD;
    padding: 40px;
  }
  
  .lateral-1 nav{
    max-width: 220px;
    padding:40px;
  }
  .lateral-1 nav ul{
    list-style: none;
  }
  
  .lateral-1 nav ul li{
    cursor:pointer;
    display: flex;
    margin: 25px 0;
  }
  .lateral-1 nav ul li a{
    display: flex;
    flex-direction: row;
    text-decoration:none;
  }

  
  .lateral-1 nav ul li a span{
    display: flex;
    color:#8DA1B5;
    font-weight: 600;
    font-size: 16px;
  }
  
  .lateral-1 nav ul li svg{
    color:#8DA1B5;
    font-size: 22px;
    margin-right: 15px;
  }
  
  .lateral-1 nav ul li.active span{
    color:#272829;
  }
  .lateral-1 nav ul li.active svg{
    display: flex;
    color:#5E81F4;
  }
  
  #logo{
    padding: 20px 40px;
    margin-top:30px;
  }
  
